export default {
	data() {
		return {
			checked:false,
			showCouponModal:false
		}
	},
	methods: {
		closeModal(){
			this.showCouponModal = false
		},
		chooseCoupon(){
			this.showCouponModal = true
		},
		choose(){
			this.checked = !this.checked
		},
		choosePayWay(){
			uni.navigateTo({
				url:'payWay'
			})
		}
	}
}
